import { Injectable } from '@angular/core';
import { Http, Response, Headers} from '@angular/http';
//import 'rxjs/add/operator/map';
import { map } from 'rxjs/operators';


@Injectable({
  providedIn: 'root'
})
export class TraveltimeDataService {

  traveldata:any;
  
  constructor(private http: Http) { }

  gettravellingdata(){
    return this.http.get('http://localhost:3000/traveller/gettime').pipe(
    map(res => res.json()));
  }

  travellingdata(traveldata){
    let headers = new Headers();
    headers.append('Content-Type','application/json');
    return this.http.post('http://localhost:3000/traveller/traveldata',traveldata,{headers:headers}).pipe(
    map(res => res.json()));
  }

  deletedata(id){
    return this.http.delete('http://localhost:3000/traveller/traveldelete/'+id).pipe(
    map(res => res.json()));
  }
}
